import { isPlatformBrowser } from '@angular/common';
import { AfterViewInit, Component, ElementRef, Inject, Input, PLATFORM_ID, ViewChild } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { FiltersPanelClass } from '@core/enums';
import { Seller } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { FiltersSortComponent } from '@feature/general/filters/components/filters-sort/filters-sort.component';
import { FiltersFacade } from '@feature/general/filters/filters.facade';
import { SellerFacade } from '@feature/seller/seller.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { CdkDropdownComponent } from '@shared/components/cdk-dropdown/cdk-dropdown.component';
import { AdsViewModel } from '@shared/facades/shared-ads.facade';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Observable } from 'rxjs';

@Component({
    selector: 'findqo-seller-ad-list',
    templateUrl: './seller-ad-list.component.html',
    styleUrls: ['./seller-ad-list.component.scss'],
    standalone: false
})
export class SellerAdListComponent implements AfterViewInit {
	constructor(
		@Inject(PLATFORM_ID) private platformId: string,
		public readonly viewport: ViewportService,
		private sellerFacade: SellerFacade,
		private readonly filtersFacade: FiltersFacade,
		private readonly matBottomSheet: MatBottomSheet,
		private readonly device: DeviceDetectorService,
		private readonly router: Router
	) {}

	@ViewChild('scrollTrigger', { read: ElementRef }) scrollTrigger: ElementRef;
	@ViewChild('adListTemplate', { read: ElementRef }) adListTemplate: ElementRef;
	@ViewChild(CdkDropdownComponent) cdkDropdown: CdkDropdownComponent;

	@Input() isPublicPage = false;
	@Input() adListVm$: Observable<AdsViewModel>;
	@Input() seller$: Observable<Seller>;
	@Input() isAgentPage;
	@Input() agentName;
	@Input() agentId;

	seller: Seller;

	readonly buttonType = ButtonType;

	isLoadingMoreAds$: Observable<boolean> = this.sellerFacade.getIsLoadingMoreAds$();
	sortList$ = this.filtersFacade.getSortList$();
	adListSkeletonArray = Array(3);
	pageNumber = 1;
	toggleSort = false;

	private intersectionObserver?: IntersectionObserver;

	private initIntersectionObserver(): void {
		if (isPlatformBrowser(this.platformId) && this.adListTemplate?.nativeElement) {
			this.intersectionObserver = new IntersectionObserver(
				(entries) => {
					entries.forEach((entry: IntersectionObserverEntry) => {
						if (entry.isIntersecting) {
							this.fetchAdsOnScroll();
						}
					});
				},
				{ rootMargin: '0px', threshold: 0.1 }
			);

			this.intersectionObserver.observe(this.scrollTrigger.nativeElement);
		}
	}

	private fetchAdsOnScroll(): void {
		const PAGE_META_DATA = this.sellerFacade.getMetadata();
		if (+PAGE_META_DATA?.totalPages > this.pageNumber) {
			this.pageNumber += 1;
			this.sellerFacade.updateAdsList({
				page: this.pageNumber,
				userId: this.sellerFacade.getSeller().id,
				sellerTypeId: this.sellerFacade.getSeller().sellerTypeId,
			});
		}
	}

	onOpenSortBottomSheet(): void {
		this.matBottomSheet
			.open(FiltersSortComponent, {
				panelClass: FiltersPanelClass.BASE_MIN_MAX_FILTER.split(/\s+/),
			})
			.afterDismissed()
			.subscribe((res) => {
				if (res) {
					this.navigateTo(res);
				}
			});
	}

	onOpenSort(): void {
		if (this.device.isMobile()) {
			this.onOpenSortBottomSheet();
			return;
		}

		this.toggleSort = true;
		this.cdkDropdown.show();
	}

	onCloseSort(reference: string): void {
		this.navigateTo(reference);
		this.toggleSort = false;
		this.cdkDropdown.hide();
	}

	navigateTo(reference: string): void {
		if (this.isAgentPage) {
			this.router.navigate([`/agent/${this.agentId}/${this.agentName}`], {
				queryParams: { sort: reference },
			});
			return;
		}

		this.router.navigate(['/' + APP_ROUTE.seller(this.seller?.id, this.seller?.profileId, this.seller?.sellerTypeId)], {
			queryParams: { sort: reference },
		});
	}

	ngAfterViewInit(): void {
		this.initIntersectionObserver();
	}

	ngOnInit(): void {
		this.seller$.subscribe((res) => {
			this.seller = res;
		});
	}
}
