import { ClipboardModule } from '@angular/cdk/clipboard';
import { Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogModule } from '@angular/material/dialog';
import { MatInputModule } from '@angular/material/input';
import { ButtonType } from '@shared/components/button/button.component';
import { ButtonModule } from '@shared/components/button/button.module';
import { DestroyService } from '@shared/services/destroy.service';
import { SharedModule } from '@shared/shared.module';
import { takeUntil, tap, timer } from 'rxjs';

@Component({
    selector: 'findqo-share-profile-dialog',
    imports: [SharedModule, ButtonModule, MatInputModule, ClipboardModule, MatDialogModule],
    templateUrl: './share-profile-dialog.component.html',
    styleUrl: './share-profile-dialog.component.scss',
    providers: [DestroyService]
})
export class ShareProfileDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data: string,
		private readonly destroyService: DestroyService
	) {}

	@ViewChild('profileLink') profileLink!: ElementRef<HTMLInputElement>;

	readonly buttonType = ButtonType;
	isCopied = false;

	copyAdLink(profileLink: string): void {
		navigator.clipboard.writeText(profileLink);
		this.isCopied = true;

		timer(5000)
			.pipe(
				takeUntil(this.destroyService),
				tap(() => (this.isCopied = false))
			)
			.subscribe();
	}

	ngAfterViewInit(): void {
		this.profileLink.nativeElement.value = this.data['profileUrl'];
	}
}
