import { DOCUMENT, isPlatformBrowser } from '@angular/common';
import { Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, Renderer2 } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { APP_ROUTE, SELLER_META_CONTENTS, SellerTypeSlug } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { PostLoginAction, SellerType } from '@core/enums';
import { Ad, Seller } from '@core/models';
import { CanonicalService } from '@core/services/canonical.service';
import { SeoService } from '@core/services/seo.service';
import { SsoPostLoginService } from '@core/services/sso-post-login.service';
import { SellerFacade } from '@feature/seller/seller.facade';
import { AdsViewModel, SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, filter, takeUntil, tap } from 'rxjs';

@Component({
    selector: 'findqo-seller.page',
    templateUrl: './seller.page.html',
    styleUrls: ['./seller.page.scss'],
    providers: [DestroyService],
    standalone: false
})
export class SellerPage implements OnInit, OnDestroy {
	constructor(
		@Inject(DOCUMENT) private readonly document: Document,
		@Inject(PLATFORM_ID) private readonly platformId: string,
		private readonly sellerFacade: SellerFacade,
		private readonly renderer: Renderer2,
		private readonly canonicalService: CanonicalService,
		private readonly sharedAdsFacade: SharedAdsFacade,
		private readonly activatedRoute: ActivatedRoute,
		private readonly destroyService: DestroyService,
		private readonly seoService: SeoService,
		private readonly ssoPostLoginService: SsoPostLoginService,
		private readonly authService: AuthService,
	) {
		this.adsVm$ = this.sharedAdsFacade.getAdsVm$();
		this.seller$ = this.sellerFacade.getSeller$();
	}

	@Input() sellerName!: string;
	@Input() sellerType!: string;
	@Input() sellerId!: string;
	@Input() isPublicPage = false;
	@Input() agentId!: string;
	@Input() agentName!: string;

	isAgentPage = false;

	adsVm$: Observable<AdsViewModel>;
	seller$: Observable<Seller>;

	private loadSellerAdList(queryParams): void {
		this.isAgentPage = !!this.agentId;

		this.sellerFacade.loadAdList({
			userId: this.sellerId,
			sellerTypeId: this.sellerType === SellerTypeSlug.INDIVIDUAL ? SellerType.INDIVIDUAL : SellerType.BUSINESS,
			sort: queryParams['sort'],
			agentId: this.agentId,
		});
	}

	private setSEO(): void {
		const URL = this.isAgentPage
			? `/agent/${this.agentId}/${this.agentName}`
			: `${APP_ROUTE.seller(this.sellerId, this.sellerName, this.sellerType === SellerTypeSlug.INDIVIDUAL ? SellerType.INDIVIDUAL : SellerType.BUSINESS)}`;
		this.canonicalService.createCannonicaUrl(URL);

		this.seller$
			.pipe(
				takeUntil(this.destroyService),
				filter((seller: Seller) => !!seller),
				tap((seller: Seller) => {
					// Only business account needs to be indexed in SERP
					if (seller.sellerTypeId === SellerType.INDIVIDUAL) {
						this.seoService.robotsNoindex();
					}
				}),
			)
			.subscribe((seller: Seller) => {
				this.seoService.setSeo({
					title: seller?.name,
					...(!!seller?.location && { description: SELLER_META_CONTENTS.description(seller.name, seller.location.address) }),
					...(!!seller?.picture && { image: seller.picture }),
				});
			});
	}

	private addToFav(ad: Ad): void {
		if (this.authService.user()?.id !== ad.seller.id) {
			ad = {
				...ad,
				isFavorite: '1',
			};
			this.sharedAdsFacade.addFavouriteAdFromList(ad);
		}
	}

	ngOnInit(): void {
		this.setSEO();

		// Watch the query param changes and load the lastest data
		this.activatedRoute.queryParams.subscribe((qp) => {
			this.loadSellerAdList(qp);
		});

		// Hide the scrollbar if it's public page
		this.renderer.addClass(this.document.body, 'hide-scrollbar');

		if (isPlatformBrowser(this.platformId)) {
			this.ssoPostLoginService.handlePostLoginWithAction({
				[PostLoginAction.ADD_TO_FAVORITE]: this.addToFav.bind(this),
			});
		}
	}

	ngOnDestroy(): void {
		this.sellerFacade.clearData();
	}
}
